import React from 'react';
import styled from 'styled-components'

import LinkedButton from '../../../components/LinkedButton'

const ServiceButton = ({ days, title, time, open, remaining, linkTo}) => {
  const getDays = (days) => {
    if (days === '수요일') {
      return 3
    } else if (days === '금요일') {
      return 5
    } else if (days === '일요일') {
      return 0
    }
  }
  let latestDay = new Date();
  latestDay.setDate(latestDay.getDate() + (getDays(days) + 7 - latestDay.getDay()) % 7);

  const handleClick = (e) => {
    if (!open) {
      e.preventDefault()
    }
  }

  return (
    <LinkedButton to={linkTo} style={{margin: '20px 0'}} onClick={handleClick}>
      <DateContainer>
        <div style={{fontSize: 24, fontWeight: 700}}>{latestDay.getMonth()+1}월</div>
        <div style={{fontSize: 24, fontWeight: 700}}>{latestDay.getDate()}일</div>
        <div style={{fontSize: 12 }}>{days}</div>
      </DateContainer>
      <InfoContainer>
        <Header>
          <div style={{fontSize: 24, fontWeight: 700}} >{title}</div>
        </Header>
        {time.length < 2 ? (
          <BodyContainer>
            <TimeContainer>{time[0]}</TimeContainer>
            <AvailableContainer>
              {!open ? (
                <text style={{fontSize: 16, fontWeight: 400, color:'black'}}>신청대기</text>
              ):(
                <>
                {open && remaining === 0 ? (
                  <text style={{fontSize: 16, fontWeight: 400, color:'black'}}>신청완료</text>
                ) : (
                  <text style={{fontSize: 16, fontWeight: 400, color:'#F97878'}}>신청가능</text>
                )}
                </>
              )}
            </AvailableContainer>
            <CounterContainer>{remaining}명/70명</CounterContainer>
          </BodyContainer>
        ):(
          <div style={{display: 'flex', flexDirection:'column'}}>
            <BodyContainer>
              <TimeContainer>{time[0]}</TimeContainer>
              <AvailableContainer>
                {!open ? (
                  <text style={{fontSize: 16, fontWeight: 400, color:'black'}}>신청대기</text>
                ):(
                  <>
                  {open && remaining === 0 ? (
                    <text style={{fontSize: 16, fontWeight: 400, color:'black'}}>신청완료</text>
                  ) : (
                    <text style={{fontSize: 16, fontWeight: 400, color:'#F97878'}}>신청가능</text>
                  )}
                  </>
                )}
              </AvailableContainer>
              <CounterContainer>{remaining}명/70명</CounterContainer>
            </BodyContainer>
            <BodyContainer>
              <TimeContainer>{time[1]}</TimeContainer>
              <AvailableContainer>
                {!open ? (
                  <text style={{fontSize: 16, fontWeight: 400, color:'black'}}>신청대기</text>
                ):(
                  <>
                  {open && remaining === 0 ? (
                    <text style={{fontSize: 16, fontWeight: 400, color:'black'}}>신청완료</text>
                  ) : (
                    <text style={{fontSize: 16, fontWeight: 400, color:'#F97878'}}>신청가능</text>
                  )}
                  </>
                )}
              </AvailableContainer>
              <CounterContainer>{remaining}명/70명</CounterContainer>
            </BodyContainer>
            
            <BodyContainer>
              <TimeContainer>{time[2]}</TimeContainer>
              <AvailableContainer>
                {!open ? (
                  <text style={{fontSize: 16, fontWeight: 400, color:'black'}}>신청대기</text>
                ):(
                  <>
                  {open && remaining === 0 ? (
                    <text style={{fontSize: 16, fontWeight: 400, color:'black'}}>신청완료</text>
                  ) : (
                    <text style={{fontSize: 16, fontWeight: 400, color:'#F97878'}}>신청가능</text>
                  )}
                  </>
                )}
              </AvailableContainer>
              <CounterContainer>{remaining}명/70명</CounterContainer>
            </BodyContainer>
            
            <BodyContainer>
              <TimeContainer>{time[3]}</TimeContainer>
              <AvailableContainer>
                {!open ? (
                  <text style={{fontSize: 16, fontWeight: 400, color:'black'}}>신청대기</text>
                ):(
                  <>
                  {open && remaining === 0 ? (
                    <text style={{fontSize: 16, fontWeight: 400, color:'black'}}>신청완료</text>
                  ) : (
                    <text style={{fontSize: 16, fontWeight: 400, color:'#F97878'}}>신청가능</text>
                  )}
                  </>
                )}
              </AvailableContainer>
              <CounterContainer>{remaining}명/70명</CounterContainer>
            </BodyContainer>
          </div>
        )}
      </InfoContainer>
    </LinkedButton>
  )
}

const DateContainer = styled.div`
  display: flex;
  flex-direction: column;
  flex: 1;
  align-items: center;
  justify-content: center;
  color: black;
`;
const InfoContainer = styled.div`
  flex: 3;
  padding-left: 10px;
  display: flex;
  flex-direction: column;
`;
const Header = styled.div`
  padding-bottom: 10px;
  color: black;
`;
const BodyContainer = styled.div`
  display: flex;
  color: black;
`;
const TimeContainer = styled.div`
  margin-right: 1rem;
  flex: 1;
`;
const AvailableContainer = styled.div`
  margin-right: 1rem;
  flex: 1;
`;
const CounterContainer = styled.div`
  flex: 1;
`;

export default React.memo(ServiceButton);